<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <v-card max-width="500" color="#f2dda8" class="mx-auto">
          <v-text-field
            solo
            placeholder="Поиск . . ."
            class="mx-2 pt-3"
            append-icon="mdi-magnify"
            outlined
            v-model="search"
          />
        </v-card>
      </v-col>
    </v-row>
    <v-row
      class="d-flex justify-space-between mx-auto"
      v-if="paginatedItems.length > 0"
    >
      <v-col
        cols="12"
        md="2"
        sm="4"
        class="mx-auto"
        v-for="book in paginatedItems"
        :key="book.id"
      >
        <BookCard :book="book" />
      </v-col>
    </v-row>
    <div class="text-center" v-if="paginatedItems.length > 0">
      <v-pagination
        v-model="page"
        :length="Math.ceil(this.filteredBook.length / this.perPage)"
        :total-visible="7"
      ></v-pagination>
    </div>
  </v-container>
</template>

<script>
import BookCard from '@/components/BookCard.vue'
import { mapState } from 'vuex'
import NProgress from 'nprogress'
export default {
  name: 'BookList',
  data() {
    return {
      search: '',
      page: 1,
      perPage: 20
    }
  },
  components: {
    BookCard
  },
  created() {
    NProgress.start()
    this.$store.dispatch('book/fetchBooks')
  },
  mounted() {
    NProgress.done()
  },
  computed: {
    ...mapState(['book']),
    filteredBook() {
      return this.book.books.filter(data => {
        let author = data.author.toLowerCase().match(this.search.toLowerCase())
        let book_name = data.title
          .toLowerCase()
          .match(this.search.toLowerCase())
        return author || book_name
      })
    },
    paginatedItems() {
      return this.filteredBook.slice(
        (this.page - 1) * this.perPage,
        this.page * this.perPage
      )
    }
  }
}
</script>

<style scoped></style>
